.App {
    text-align: center;
}

.outcrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.popup-close-button {
    border-radius: 0px;
    bottom: 5%;
    right: 5%;
    position: fixed;
    background: #6dee5c;
    border: 1px solid black;
    color: black;
}

.popup {
    z-index: 1px;
    width: 400px;
    height: 500px;
    background: #b8b8b8;
    border: 4px solid #070707;
    border-radius: 6px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
.filter-button {
    width: 60px;
    border-radius: 0px;
    color: "black";
    background-color: #787a7e;
    border: 1px solid black;
}
.App-link {
    color: #61dafb;
}

.item-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 20px;
    padding: 20px;
}

.item {
    border: 1px solid #6dee5c;
}

.side-shadow {
    box-shadow: 12px 0 15px -4px rgba(0, 0, 0, 0.2),
        -12px 0 8px -4px rgba(0, 0, 0, 0.2);
    background-color: white;
}

.ez-bg {
    background-image: url(https://i.ibb.co/5xctNYH/ezbg.png);
}

.overflow-y-scroll {
    overflow-y: scroll;
}

.h-20 {
    height: 20%;
}

.h-80 {
    height: 80%;
}
